import { Injectable } from '@angular/core';
import "rxjs/add/operator/map";
import { Http } from "@angular/http";

@Injectable()

export class OnlineService {

  constructor(private http: Http) { }

  login(username, password) {
    return this.http.post("http://localhost:3000/auth/login", {
      username: username,
      password: password
    }).map(res => res.json());
  }

  signup(username, password) {
    return this.http.post("http://localhost:3000/auth/signup", {
      username: username,
      password: password
    }).map(res => res.json());
  }

  addcontact(name, email, phone, holder) {
    return this.http.post("http://localhost:3000/addcontact", {
      name: name,
      email: email,
      phone: phone,
      holder: holder
    }).map(res => res.json());
  }

  deletecontact(id, holder) {
    return this.http.post("http://localhost:3000/delete", {
      id: id,
      holder: holder
    }).map(res => res.json());
  }

  getContacts(holder) {
    return this.http.get("http://localhost:3000/contacts/"+holder).map(res => res.json());
  }

}



