import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators} from "@angular/forms";
import { Router } from "@angular/router";
import { OnlineService } from "../online.service";

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  loginForm: FormGroup;

  constructor(private fb: FormBuilder, private online: OnlineService, private route: Router) {
    this.loginForm = fb.group({
      "username": [null, Validators.compose([Validators.required, Validators.minLength(3)])],
      "password": [null, Validators.compose([Validators.required, Validators.minLength(6)])]
    });
  }

  ngOnInit() {
  }

  login(value) {
    if(value.username == "" || value.password == "") {
      alert("Please use valid data to login");
    } else {
      this.online.login(value.username, value.password).subscribe(result => {
        if(result.ok == false) {
          alert(result.message);
        } else {
          window.localStorage.setItem("username", value.username);
          this.route.navigate(['/'], {
            queryParams: { username: value.username }
          });
        }
      });
    }
  }

}



