import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators} from "@angular/forms";
import { OnlineService } from "../online.service";
import { Router } from "@angular/router";

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  addForm: FormGroup;
  contacts: Contact[];

  constructor(private fb: FormBuilder, private router: Router, private online: OnlineService) {
    this.addForm = fb.group({
      "name": [null, Validators.compose([ Validators.required ])],
      "phone": [null, Validators.compose([ Validators.required ])],
      "email": [null, Validators.compose([ Validators.email, Validators.required])]
    });
    this.online.getContacts(window.localStorage.getItem("username")).subscribe(result => {
      this.contacts = result.data;
    })
  }

  ngOnInit() {
  }

  addContact(value) {
    var username = window.localStorage.getItem("username");
    this.online.addcontact(value.name, value.email, value.phone, username).subscribe(result => {
      if(result.ok == false) {
        alert(result.message);
      } else {
        alert(result.message);
        window.location.reload();
      }
    });
  }

  delete(id) {
    var holder = window.localStorage.getItem("username");
    this.online.deletecontact(id, holder).subscribe(result => {
      if(result.ok == true) {
        this.contacts = result.data
      } else {
        alert(result.message);
      }
    });
  }

}

interface Contact {
  name: String;
  email: String;
  phone: Number;
  holder: String;
}



